.btn_arrow_right {
  position: relative;
}
.btn_arrow_right::after {
  content: "";
  width: 12px;
  height: 12px;
  background: url(/src/images/common/arrow-right-blue.png) no-repeat center center/contain;
  position: absolute;
  top: 50%;
  right: 0.86em;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease 0s;
}
.btn_arrow_right:hover::after, .btn_arrow_right:focus::after {
  background: url(/src/images/common/arrow-right-white.png) no-repeat center center/contain;
  right: -1%;
}

.contentLink {
  display: inline-block;
  max-width: 335px;
  width: 100%;
  padding: 0.543em 1em;
  border-radius: 0.75rem;
  background-color: #FFF;
  color: #1391E6;
  font-size: 1.25rem;
  font-weight: 700;
  letter-spacing: 0.08em;
  text-align: center;
  border: 2px solid currentColor;
  transition: all 0.3s ease 0s;
  position: relative;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .contentLink {
    padding: 0.56em 1.214em;
    font-size: 1.5rem;
    width: auto;
    max-width: none;
  }
}
.contentLink::after {
  content: "";
  position: absolute;
  background: url(/src/images/common/arrow-down.png) no-repeat center center/contain;
  width: 1.25rem;
  height: 1.25rem;
  right: 50%;
  left: 50%;
  bottom: 5%;
  transform: translate(-50%, 110%);
  transition: 0.3s ease-in-out;
}
.contentLink:focus, .contentLink:hover {
  background-color: #DFF1FD;
  opacity: 1;
}
.contentLink:focus::after, .contentLink:hover::after {
  transform: translate(-50%, 0%);
}

.contentLink__text {
  display: inline-block;
  transition: 0.3s ease-in-out;
}

.contentLink:hover .contentLink__text {
  transform: translateY(-10px);
}

